    height: 100%;
    padding: 5px;
    font-size: 15px;
    margin: 5px;
    width: 100%;
    
    .subcomponent-container {
        font-size: 15px;
        margin: auto;
        overflow: scroll;
        width: 100%;   

        .notes {
